@font-face {
  font-family: "KumarOne-Regular";
  src: url("/fonts/KumarOne-Regular.ttf");
}
body {
  background-image: url(/images/body_logo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}

.sw_nav {
  position: relative;
  display: block;
  height: 1000px;
  background-image: url(/images/nav_logo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 45px;
  width: 1500px;
  margin: 0 auto;
  box-sizing: border-box;
  margin-top: 100px;
  margin-bottom: 100px;
  overflow: hidden;
}

.nav_buttons {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding-top: 100px;
  gap: 200px;
}

.description_item {
  margin: 0 auto;
  background-image: url(/images/label_logo.jpg);
  color: #FFF;
  margin-top: 50px;
  opacity: 0;
  width: 500px;
  border-radius: 10px;
  max-height: 700px;
  overflow: auto;
  padding-bottom: 50px;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
}

.hidden {
  display: none;
}

.active {
  opacity: 1;
  transition: 2s ease-in-out;
}

.people {
  display: block;
}

button {
  width: 300px;
  height: 50px;
  font-size: 28px !important;
  border-radius: 10px !important;
  box-shadow: 0px 0px 20px blue;
  border: 0px;
  cursor: pointer;
  font-family: "KumarOne-Regular" !important;
}

button:hover {
  transition: ease-in-out 0.1s;
  box-shadow: 0px 0px 20px red;
  height: 60px;
  width: 320px;
}

.data {
  padding-left: 50px;
  padding-top: 20px;
  font-size: 26px;
}

.load-more {
  padding-top: 50px;
  padding-bottom: 20px;
  margin-left: 84px;
}

.data > div {
  margin-top: 20px;
  cursor: pointer;
  width: fit-content;
  font-family: "KumarOne-Regular", sans-serif;
}

.data > div:hover {
  color: red;
}

.description_full {
  width: 300px;
  height: 300px;
  background-color: aliceblue;
}

.modal-body {
  font-size: 20px;
  text-align: center;
  font-family: "KumarOne-Regular";
}

.modal-header {
  background-color: #222;
  color: #FFF;
  font-family: "KumarOne-Regular";
}

.btn-close {
  background-color: #FFF !important;
  color: #FFF !important;
}

.btn-close:hover {
  width: 30px;
  height: 30px;
}

.btn-secondary {
  background-color: #222 !important;
}